import { Formik} from 'formik';
import { Input, Form, FormItem, Checkbox } from 'formik-antd'
import * as Yup from 'yup';
import s from '../Login/Login.module.css';
import { NavLink } from 'react-router-dom';
import { Button } from 'antd';

/* matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{2,})/,
"   5  255 ,   ,  ,      "
) */

const RegistrationForm = (props) => {
	const activeLink = ({isActive}) => isActive ? s.form_navigate_el_active : s.form_navigate_el; 	//   

	return (
		<Formik
			initialValues={{ email: '', password: '', repeatPassword: '', name: '', secondName: '', middleName: '', is18: false}}
			validationSchema={Yup.object({
				email: Yup.string().email(' email').required('').max(255, 'Email     255 '),
				password: Yup.string().min(5, '   5  255 ').max(255, "   5  255 ").required(''),
				repeatPassword: Yup.string().when("password", {
					is: val => (val && val.length > 0 ? true : false),
					then: Yup.string().oneOf(
					  [Yup.ref("password")],
					  "  "
					)
				  }).required(''),
				name: Yup.string().max(32, '     32 ').required(''),
				secondName: Yup.string().max(32, '     32 ').required(''),
				middleName: Yup.string().max(32, '     32 '),
				is18: Yup.bool().oneOf([true], ' ')
			  })}
			onSubmit={(values, { setSubmitting, setStatus }) => {
				console.log(JSON.stringify(values, null, 2));
				let user = {
					"name":  values.secondName + ' ' + values.name + ' ' + values.middleName,
					"email": values.email,
					"password": values.password
				}
				props.addUser(user);
				values.password = '';
				setSubmitting(false);
			}}
		>
			{({ isSubmitting, status }) => (
				<Form className={s.form}>
					<div className={`${s.formWrapper} ${status && s.error}`}> 
						<div className={s.form_navigate}>
							<NavLink to={'/login'} className={activeLink}></NavLink>
							<NavLink to={'/register'} className={activeLink}></NavLink>
						</div>
						<div className={s.line_navigate}></div>
						<div className={s.errorText}>{status}</div> 
						<FormItem name="name" >
							<Input name="name" placeholder="*" className={s.elemForm}/>
						</FormItem>
						<FormItem name="secondName" >
							<Input name="secondName" placeholder="*" className={s.elemForm}/>
						</FormItem>
						<FormItem name="middleName" >
							<Input name="middleName" placeholder="" className={s.elemForm}/>
						</FormItem>
						<FormItem name="email" >
							<Input type="email" name="email" placeholder="Email*" className={s.elemForm}/>
						</FormItem>
						<FormItem name="password" >
							<Input.Password type="password" name="password" placeholder="Password*" className={s.elemForm}/>
						</FormItem>
						<FormItem name="repeatPassword" >
							<Input.Password type="password" name="repeatPassword" placeholder=" *" className={s.elemForm}/>
						</FormItem>
						<FormItem name="is18" >
							<Checkbox name="is18">,   18 </Checkbox>
						</FormItem>
						<Button className={s.bLogin} type="primary" htmlType="submit" disabled={isSubmitting}></Button>
					</div>
				</Form>
			)}
		</Formik>
  );
}

export const Registration = (props) => {

	return ( 
		<div>
			<RegistrationForm {...props}/>
		</div>
	 );
}
